import { actionTypes } from "../actions/controlActions";

export const locationReducer = (state, action) => {
  if (state === undefined) {
    return {
      coordinates: {
        lat: null,
        lng: null
      },
      string: ""
    };
  }
  let newState = { ...state };
  switch (action.type) {
    case actionTypes.setLocation:
      newState = {
        coordinates: action.coordinates,
        string: action.string
      };
      break;
    default:
      return state;
  }
  return newState;
};



